import React, { useState, useEffect } from 'react';
import styles from '../styles/components/TextField.module.css';
import classNames from 'classnames';

type TextInputProps = {
  initialText: string; // 
  maxLength: number; //  
  disabledPlaceholder?: string; //  placeholder
  disabled?: boolean; //  
  readOnly?: boolean; //  
};

const TextInput: React.FC<TextInputProps> = ({
  initialText,
  maxLength,
  disabledPlaceholder,
  disabled = false,
  readOnly = false,
}) => {
  const [text, setText] = useState(initialText); //  text 
  const [isChanged, setIsChanged] = useState(false); // text  

  const placeholder = disabledPlaceholder
    ? disabledPlaceholder
    : '  ';

  //     
  useEffect(() => {
    setIsChanged(text !== initialText);
  }, [text, initialText]);

  const handleTextChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    if (event.target.value.length <= maxLength) {
      setText(event.target.value);
    }
  };

  const handleSave = () => {
    alert(`  : ${text} `);
  };

  return (
    <div className={styles['text-field']}>
      <div className={styles['textarea-container']}>
        <textarea
          className={classNames(styles['text-area'], {
            [styles.disabled]: disabled,
            [styles.readonly]: readOnly,
          })}
          value={text}
          aria-placeholder={placeholder}
          placeholder={placeholder}
          onChange={handleTextChange}
          disabled={disabled || readOnly}
          aria-label=" "
          aria-readonly={readOnly}
        />
        <div
          className={classNames(styles['characters-remaining'], {
            [styles.disabled]: disabled || readOnly,
          })}
          aria-label="  "
        >
          {maxLength - text.length} / {maxLength}
        </div>
      </div>

      {isChanged && (
        <button
          type="button"
          aria-label=""
          className={styles['save-button']}
          onClick={handleSave}
          disabled={!isChanged || disabled}
          aria-disabled={!isChanged || disabled}
        >
          Save
        </button>
      )}
    </div>
  );
};

export default TextInput;
